main {
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  .contact {
    display: none;
    padding: 1rem;
    border-radius: 0.4rem;
    font-family: exo;
    border-top: solid 0.2rem;
    border-bottom: solid 0.2rem;
    border-top-right-radius: 5rem;
    border-bottom-left-radius: 5rem;
    border-top-left-radius: 5rem;
    border-bottom-right-radius: 5rem;
    box-shadow: 0 0.1rem 0.4rem #c2bdb2;
    margin-top: 2rem;

    .title {
      font-family: "exo";
      letter-spacing: 0.1rem;
      padding: 0.3rem;
      font-size: 1.3rem;
      border-radius: 1rem;
      text-transform: uppercase;
      text-shadow: 0 0.1rem 0.1rem #f7f7f7;
      text-align: center;
      color: #222221;

      @media only screen and (orientation: landscape) {
        font-size: 2rem;
      }
    }

    .text {
      margin-left: 0.5rem;
      padding-right: 30vw;
      font-size: 0.8rem;
      font-family: "exo";
      color: #0b151f;

      @media only screen and (orientation: landscape) {
        font-size: 1rem;
      }
    }

    .button {
      position: absolute;
      right: 25vw;
      margin-top: -8rem;
      border: none;
      cursor: pointer;
      line-height: 3.3rem;
    }

    .icon,
    .letters {
      display: block;
      position: absolute;
      width: 21vw;
      letter-spacing: 0.1rem;
      height: 3.3rem;
      font-family: exo;
      font-size: 0.95rem;
      box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
        0.04rem 0.04rem 0.04rem 0rem #0000001a,
        0.06rem 0.06rem 0.06rem 0rem #0000001a;
      text-align: center;
      box-sizing: border-box;
      transition: all 0.3s;

      @media only screen and (orientation: landscape) {
        font-size: 1.1rem;
      }

      .icon {
        font-size: 1.5rem;
        transform: rotateX(90deg);
        transform-origin: 50% 50% -1.53rem;
      }

      .letters {
        transform: rotateX(0deg);
        transform-origin: 50% 50% -1.53rem;
      }

      &:hover {
        .icon {
          box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
            0.07rem 0.07rem 0.12rem 0rem #0000001a,
            0.04rem 0.04rem 0.05rem 0rem #0000001a;
          transform: rotateX(0deg);
        }

        .letters {
          box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
            0.07rem 0.07rem 0.12rem 0rem #0000001a,
            0.04rem 0.04rem 0.05rem 0rem #0000001a;
          color: transparent;
          transform: rotateX(-90deg);
        }
      }
    }
  }
}

.home {
  .vid {
    margin-top: -1rem;
    .src {
    }
  }

  .checkout,
  .indicators {
    margin: 0.2rem;
  }
  .checkout {
    filter: drop-shadow(0 0.2rem 0.1rem #f8c360);
    background: linear-gradient(-45deg, #fca849, #f8e091, #fca849);
    position: relative;
    font-family: exo;
    text-align: center;
    font-size: 1.1rem;
    display: inline-block;
    border-top-right-radius: 2rem;
    border-bottom-left-radius: 2rem;
    padding: 0.6rem;
    color: #000000;
    text-shadow: -0.06rem -0.06rem #f8f7f3, 0.06rem -0.06rem #f8f7f3,
      -0.06rem 0.06rem #f8f7f3, 0.06rem 0.06rem #f8f7f3;
  }

  .indicators {
    filter: drop-shadow(0 0.1rem 0.1rem #faf0f0);
    background: linear-gradient(-45deg, #fca849, #f8e091, #fca849);
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 97vw;
    justify-content: space-evenly;
    overflow: hidden;
    height: 8rem;
    border-bottom-right-radius: 2rem;
    border-top-left-radius: 2rem;

    .button {
      cursor: pointer;
      position: relative;
      border-radius: 1rem;

      .background {
        padding: 0.4rem;
        clip-path: circle(45% at 50% 50%);
        height: 4.5rem;
        display: block;
      }

      .sub-title {
        font-family: goldman;
        padding: 1.7rem 0;
        background: #494646;
        border-radius: 50%;
        text-shadow: 0 0.1rem 0.1rem #050505;
        border: solid 0.3rem #faf8f2;
        font-size: 0.5rem;
        width: 4.4rem;
        color: #fcfcfc;
        text-align: center;
        filter: drop-shadow(0rem 0.1rem 0.1rem #1a1a196b);
        transition: transform 0.4s, color 0.5s, border-color 0.1s ease-in-out;

        &:hover {
          background: #f8f7f3;
          color: #494646;
          text-shadow: 0 0.1rem 0.1rem #aaa4a4;
          border: solid 0.3rem #494646;
          transform: translateY(-7%);
          filter: drop-shadow(0rem 0.2rem 0.1rem #b3acac);
        }
      }
    }
  }

  .slider-button {
    color: #d6dde6;
    border-radius: 0.2rem;
    height: 3rem;
    position: absolute;
    filter: drop-shadow(0rem 0.2rem 0.1rem #e2e2e0);
  }

  .slide {
    width: 90vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3.5rem;
    text-shadow: 0 0.1rem 0.1rem wheat;
    padding: 1rem;
    transition: transform 0.5s ease-in-out;

    .text {
      text-align: center;
      font-family: exo;
      font-size: 0.8rem;
      color: #000000;
      margin: 1rem 0.5vw 2rem 0.5vw;
      width: 85vw;
    }

    .link {
      position: relative;
      bottom: 1rem;
      background: #494646;
      display: flex;
      width: 100vw;
      height: 4.5rem;
      flex-direction: column;
      align-items: center;
      text-decoration: none;
      transition: filter 0.2s ease-in-out, transform 0.25s ease-in-out;
      filter: drop-shadow(0rem 0.1rem 0.1rem #ffffff);

      &:hover {
        transform: scale(1.02);
        background: linear-gradient(-45deg, #fca849, #f8e091, #fca849);
      }

      .to {
        position: relative;
        display: flex;
        flex-direction: row;
        height: 100%;
        align-items: center;
        color: #fdf5f5;
        text-shadow: 0 0.1rem 0.1rem #131212;
        font-size: 1rem;
        overflow: hidden;
        font-family: exo;
        margin-block-start: 0;
        margin-block-end: 0;

        .icon {
          margin-top: 0.3rem;
          padding: 0.2rem;
        }

        &:hover {
          color: #494646;
          text-shadow: 0 0.1rem 0.1rem #d8d1d1;
        }

        .image {
          margin-right: 2vw;
          height: 4rem;
          filter: drop-shadow(0rem 0.2rem 0.3rem #afada7);
        }
      }
    }
  }

  .contact_form {
    font-family: exo;
    width: 70vw;
    margin-top: 1rem;
    padding: 0.5rem;
    filter: drop-shadow(0 0.1rem 0.1rem #f7bb4e);
    background: linear-gradient(to right, #fdc775, #eed566, #fcda6a);
    border-radius: 0.4rem;

    .title {
      color: #1d1d1c;
      font-size: 1rem;
      width: 62vw;
      border-radius: 3rem;
      margin-left: 5vw;
      text-shadow: 0 0.07rem 0rem #c0b7b7;
      text-align: start;
      text-transform: uppercase;
    }

    .form {
      margin-left: 3vw;
      display: flex;
      flex-direction: column;
      width: 50vw;

      .field {
        text-shadow: 0 0.07rem 0rem #968f8f;
        padding: 0.1rem;
        border-top-right-radius: 0.5rem;
        filter: drop-shadow(0rem -0.02rem 0.1rem #e0e0dd);
        background: #494646;
        font-size: 1rem;
        color: #ffffff;
        text-shadow: 0 0.1rem 0.1rem #141313;
        margin-top: 1rem;
        font-size: 0.8rem;
      }

      .input {
        position: relative;
        width: 45.8vw;
        padding: 0.6rem;
        border: none;
        border-bottom-right-radius: 0.5rem;
        filter: drop-shadow(0rem 0.1rem 0.1rem #848686);

        &.message {
          width: 46vw;
          height: 6rem;
          resize: none;
        }
      }

      .choice {
        filter: drop-shadow(0rem 0.1rem 0.1rem #848686);
        padding: 0.8rem;
        border-radius: 0.3rem;
        font-size: 0.75rem;
        width: 50vw;
        font-family: exo;
        text-transform: uppercase;
        border: none;
      }

      .required {
        color: red;
        font-size: 1.2rem;
        font-style: bold;
      }

      .send {
        border: none;
        color: #0e0d0d;
        background: #fff9f5;
        font-family: goldman;
        padding: 0.7rem;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 0.8rem;
        margin: 1rem;
        border-radius: 0.5rem;
        transition: filter 0.1s ease-in-out, transform 0.25s ease-in-out,
          background-size 0.3s ease-out;

        &:hover {
          background: #a3bbfd;
          transform: scale(1.05);
        }
      }
    }
  }
}
