.aida {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: exo;
  font-size: 0.8rem;
  border-radius: 0.4rem;
  filter: drop-shadow(0rem 0.2rem 0.2rem #676568);

  .title {
    position: relative;
    font-family: goldman;
    font-size: 1.5rem;
    text-shadow: -0.06rem -0.06rem #080808, 0.06rem -0.06rem #080808,
      -0.06rem 0.06rem #080808, 0.06rem 0.06rem #080808;
  }

  .background {
    position: relative;
    filter: drop-shadow(0rem 0.2rem 0.3rem #676568);
    clip-path: polygon(0 0, 100% 0, 100% 65%, 50% 100%, 0 65%);
    padding-bottom: 2.5rem;
    border-radius: 0.5rem;

    .image {
      height: 8.5rem;
      position: relative;
    }
  }

  .container {
    position: relative;
    padding: 1rem;
    border-radius: 0.4rem;
    display: flex;
    align-items: center;

    &:nth-child(odd) {
      flex-direction: row-reverse;
      text-align: end;
    }

    .art {
      height: 6.5rem;
      filter: drop-shadow(0rem 0.2rem 0.1rem #f1f5f7);
    }

    &.desire {
      flex-direction: row;
    }

    &.interest,
    &.action {
      text-align: end;
      flex-direction: row-reverse;
    }
  }
}

.article {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: exo;
  font-size: 1rem;
  border-radius: 0.4rem;

  .background {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100vw;

    .title {
      z-index: 1;
      color: #11151b;
      filter: drop-shadow(0rem 0.1rem 0.1rem #f1f5f7);
      font-size: 1rem;
      padding: 0.15rem;
      font-family: goldman;
      text-transform: uppercase;
    }

    .image {
      z-index: 0;
      width: 45vw;
      position: absolute;
    }
  }

  .content {
    width: 84vw;

    .intro {
      font-size: 0.85rem;
    }
  }

  .container {
    display: flex;
    align-items: center;
    flex-direction: column;

    .sub-title {
      text-align: center;
      padding: 0.2rem;
      font-size: 0.9rem;
    }

    .sub-text {
      font-size: 0.8rem;
      padding: 0.5rem;
      margin: 0 2vw 0 2vw;
    }
  }

  .ending {
    padding: 1.2rem;
    font-size: 1rem;

    @media only screen and (orientation: portrait) {
      font-size: 0.85rem;
    }
  }

  &.process {
    margin-top: 3rem;

    .background {
      .title {
        text-align: end;
        width: 60vw;
        padding: 0.5rem 3vw 0.5rem 0;
        margin: 3rem 0 4rem 40vw;
      }

      .image {
        left: 2vw;
      }
    }

    .container {
      align-items: flex-start;

      &:last-child {
        border-top-left-radius: 0.5rem;
        border-top-right-radius: 0.5rem;
      }

      .sub-title {
        text-align: start;
        width: 80vw;
        margin-left: -4vw;
        clip-path: polygon(
          0 5%,
          60% 5%,
          85% 0,
          100% 50%,
          85% 100%,
          60% 95%,
          0 95%
        );
      }

      .sub-text {
      }
    }

    .ending {
      border-color: #f7f7f7;
      border-top-left-radius: 2rem;
      border-bottom-left-radius: 2rem;
    }
  }

  &.technologies {
    margin-top: 4.5rem;

    .background {
      .title {
        width: 100vw;
        padding: 3rem 0 3rem 0;
        text-align: center;
      }

      .image {
        width: 100vw;
      }
    }

    .content {
      border-radius: 0.5rem;

      .intro {
        margin-top: 2rem;
        padding: 0.7rem;
      }
    }

    .container {
      .sub-title {
        border-top: solid 0.2rem;
        border-bottom: solid 0.2rem;
        width: 80vw;
      }
    }

    .ending {
      border-top: solid 0.2rem;
      border-bottom: solid 0.2rem;
      border-color: #f7f7f7;
    }
  }

  &.ppc,
  &.seo,
  &.social,
  &.content,
  &.email {
    position: relative;

    .background {
      width: 80vw;

      .image {
        position: relative;
        top: 3rem;
        z-index: -1;
      }
    }
  }

  &.trends {
    margin-top: 1rem;

    .background {
      .title {
        margin: 0 0 2rem 15vw;
        padding: 13rem 15vw 0 6vw;
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        background: linear-gradient(90deg, #f59de9, transparent);
      }

      .image {
        right: 3vw;
      }
    }
  }

  &.services {
    .title {
      right: 47vw;
    }
  }

  &.advantages {
    margin-top: 2rem;
    width: 85vw;
    filter: drop-shadow(0 0.1rem 0.1rem #85857d);

    .container {
      .sub-title {
        padding: 0.25rem;
        width: 55vw;
        background: #dbca9a;
        filter: drop-shadow(0 0.1rem 0.1rem #888b88);
      }
      .sub-text {
        text-align: end;
      }
    }
  }
}

.list {
  border-radius: 0.4rem;
  padding: 2rem 2vw 1rem 2vw;

  .title {
    font-family: "goldman";
    font-size: 2rem;
    text-align: center;
    margin: 2vw 2vw 2rem 0;
  }

  .sub-title {
    text-align: center;
    font-family: "exo";
    text-transform: uppercase;
    font-size: 1.1rem;
    color: #161616;
  }

  .topic-title,
  .benefits-title {
    text-transform: uppercase;
    text-align: start;
    font-family: "goldman";
    font-size: 1rem;
    color: #161616;
    padding: 0.2rem;
    border-radius: 0.3rem;
    background: white;
  }

  .container {
    list-style-type: square;
    font-family: "exo";
    text-align: start;
  }

  .ending {
    padding: 1rem;
    font-family: "exo";
    text-transform: uppercase;
    font-size: 1.2rem;
    text-align: center;
    margin: 2rem 0vw 2rem 0;
    border-radius: 0.3rem;
    filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
    color: #161616;
    background-color: white;
  }
}

.couple {
  margin: 2rem 2vw 0 2vw;

  .title {
    font-size: 1.2rem;
    text-align: center;
    font-family: exo;
  }

  .container {
    padding: 0.3rem;

    .sub-title {
      font-size: 1rem;
      letter-spacing: 0.2rem;
      border-radius: 0.2rem;
      text-align: center;
      padding: 0.2rem;
    }

    .man,
    .woman {
      padding: 0.15rem;
      margin: 0.2rem;
      border-radius: 0.3rem;
      font-size: 0.8rem;
    }

    .first,
    .second {
      font-family: exo;
      padding: 0.3rem;
      font-size: 0.8rem;
    }
  }
  .ending {
    text-align: center;
    padding: 0.5rem;
    font-family: exo;
    font-size: 0.85rem;
  }
}

.webapp {
  .aida {
    .title {
      color: #3d7aeb;
    }

    .background,
    .container {
      background: linear-gradient(90deg, #487cca, #95a3f3);
    }
  }

  .article {
    &.process {
      .background {
        background: linear-gradient(-90deg, #a4c2ee, #aeb9f765);

        .title {
          filter: drop-shadow(0.1rem -0.2rem 0.2rem #f8f1e7);
          box-shadow: 0.1 -0.1rem 0.1rem #ddbda3;
          color: #353433;
        }

        .image {
          width: 40vw;
          margin-top: 2.7rem;
          margin-left: 1vw;
        }
      }

      .content {
        background: linear-gradient(-90deg, #93b9f1, #aeb9f765);
        box-shadow: 0rem 0rem 0.2rem 0rem #16161680;

        .sub-title {
          background: linear-gradient(0deg, #fdc063, #fff3b1);
        }
      }

      .ending {
        border-top: solid 0.2rem;
        border-bottom: solid 0.2rem;
        border-color: white;
        box-shadow: 0 0.1rem 0.1rem #94989b;
      }
    }

    &.technologies {
      .background {
        .title {
          background: #ffffff8a;
        }
      }

      .content {
        background: linear-gradient(20deg, #93b9f1, #aeb9f765);

        .container {
          .sub-title {
            border-color: #fffefc;
            filter: drop-shadow(0rem 0.1rem 0.1rem #f7f6f1);
            background: linear-gradient(0deg, #fdc063, #f7e061);
          }
        }

        .ending {
          border-color: #fffefc;
        }
      }
    }
  }

  .list {
    background: linear-gradient(90deg, #bcb3ab, #73645f);
    filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
  }
}

.marketing {
  .aida {
    .title {
      color: #ec9f39;
    }

    .background,
    .container {
      background: linear-gradient(90deg, #fcba41, #feb47b);
    }
  }

  .couple {
    .container {
      .sub-title {
        color: #111111;
        filter: drop-shadow(0rem 0.1rem 0.1rem #504f4ea2);
        background: linear-gradient(90deg, #fcba41, #feb47b);
        text-shadow: 0 0rem 0.1rem #dbd1d1;
      }

      .man,
      .woman {
        filter: drop-shadow(0rem 0.2rem 0.1rem #c4bfb6);
      }

      .first {
        .man {
          background: linear-gradient(-45deg, #f3dc58, #ecff3e);
        }
      }
      .second {
        .woman {
          background: linear-gradient(-45deg, #5dbe50, #80e473);
        }
      }
    }

    .ending {
      filter: drop-shadow(0rem 0rem 0.2rem #869799);
      border-top: solid 0.35rem #fcfdfd;
      border-bottom: solid 0.35rem #fcfdfd;
      color: #0f0f11;
      background: linear-gradient(0deg, #fcba41, #feb47b);
      text-shadow: 0 0rem 0.1rem white;
      border-radius: 0.5rem;
    }
  }

  .article {
    &.ppc,
    &.seo,
    &.social,
    &.email,
    &.content {
      .background {
        .image {
          width: 35vw;
          top: 0;
          z-index: 1;
          filter: drop-shadow(0 0.1rem 0.2rem #afab9d);
        }
      }
    }

    &.ppc {
      .background {
        margin-top: 2rem;
        .title {
          margin: 1vw;
          border-radius: 0.5rem;
          background: #fcd283;
          border: solid 0.15rem #f3870c;
          box-shadow: 0 0.1rem 0.1rem #a19898;
        }
      }

      .container {
        .sub-title,
        .sub-text {
          border-radius: 0.5rem;
        }
        .sub-title {
          border: solid 0.15rem #f3870c;
          background: #fcd283;
          box-shadow: 0 0.1rem 0.1rem #999480;
        }
        .sub-text {
          text-align: end;
          background: linear-gradient(90deg, transparent, #fcd28377);
        }
      }
    }

    &.seo {
      .background {
        flex-direction: row-reverse;
        .image {
          margin-right: 7vw;
        }
      }
      .content {
        .intro {
          color: #111111;
          padding: 0.3rem;
          border-radius: 0.3rem;
          filter: drop-shadow(0rem 0.1rem 0.1rem #e78727a2);
          text-shadow: 0 0rem 0.1rem #dbd1d1;
          background: linear-gradient(90deg, #fcba41, #feb47b);
        }
      }
      .container {
        border-radius: 0.3rem;
        filter: drop-shadow(0rem -0.1rem 0.1rem #f0efeea2);
        text-shadow: 0 0rem 0.1rem #dbd1d1;
        background: linear-gradient(90deg, #fcba41, #feb47b);

        .sub-title {
          text-transform: uppercase;
        }
      }
    }

    &.social {
      .background {
        filter: drop-shadow(0rem 0.1rem 0.1rem #504f4ea2);
        background: linear-gradient(-90deg, #f7f8a6, #e4985f);
        text-shadow: 0 0rem 0.1rem #dbd1d1;
        border-radius: 0.3rem;
        padding: 0.3rem;
      }
      .content {
        margin-top: 0.8rem;
        .intro {
          text-align: end;
        }
      }
      .container {
        margin: 0.3rem;
        background: linear-gradient(90deg, #f7f8a6, #f8b27d);
        border-radius: 0.3rem;
        text-shadow: 0 0rem 0.1rem #dbd1d1;
        .sub-title {
          text-transform: uppercase;
        }
      }
    }

    &.email {
      .background {
        flex-direction: row-reverse;
        padding: 0.3rem;
        border-radius: 0.3rem;
        background: linear-gradient(-90deg, #88765f8e, transparent, #88765f8e);
        box-shadow: 0 -0.1rem -0.2rem #f3f1ea;
        .title {
          margin-left: 10vw;
        }
      }
      .content {
        margin-top: 0.8rem;
        padding: 0.3rem;
        border-radius: 0.3rem;
        background: linear-gradient(-90deg, #fcb354d7, #f5ba4c);
        box-shadow: 0 0.1rem 0.2rem grey;

      }
    }

    &.content {
      .background {
        margin-top: 2rem;
      }
      .content {
        .intro {
          margin: 1rem;
          padding: 1rem;
          border-top: solid 0.2rem #feb47bcc;
          border-bottom: solid 0.2rem #feb47bcc;
          border-top-right-radius: 1.55rem;
          border-bottom-left-radius: 1.55rem;
          background: linear-gradient(90deg, #fcbb41c7, #feb47bcc);
          text-shadow: 0 0.1rem 0.1rem #f5deb3;
          filter: drop-shadow(0 0.1rem 0.1rem #f7d2c0);
        }
      }
      .container {
        .sub-title {
          width: 50vw;
          border-top: solid 0.2rem #feb47bcc;
          border-bottom: solid 0.2rem #817979;
          border-top-right-radius: 1.55rem;
          border-bottom-left-radius: 1.55rem;
          background: linear-gradient(90deg, #fcba41, #feb47b);
          text-shadow: 0 0.1rem 0.1rem wheat;
          text-transform: uppercase;
          filter: drop-shadow(0 0.1rem 0.1rem #c5bebe);
        }
        .sub-text {
          width: 60vw;
        }
      }
    }
  }
}

.graphics {
  .aida {
    .title {
      color: #9963a0;
    }

    .background,
    .container {
      background: linear-gradient(90deg, #84618b, #d8b5e0);
    }
  }

  .article {
    &.trends {
      .container {
        .sub-title {
          width: 70vw;
          border-radius: 2rem;
          box-shadow: 0 0.1rem 0.1rem #988b9b;
          text-shadow: 0.1rem 0.1rem #f5deb38c;
          background: linear-gradient(0deg, #a169b8, #eba4fd);
        }
      }
      .ending {
        width: 70vw;
        border-top-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
        box-shadow: 0 0.1rem 0.2rem #9b8b8b;
        background: linear-gradient(90deg, #d478f8, #eba4fd);
      }
    }

    &.process {
      .background {
        background: linear-gradient(-90deg, #f6ddf7, #eec5f8d7);

        .title {
          background: linear-gradient(-90deg, #b676d3cb, #d874f100);
          filter: drop-shadow(0.1rem -0.2rem 0.2rem #faf1e4);
          color: #353433;
        }

        .image {
          margin-left: 1vw;
          margin-bottom: 2.2rem;
        }
      }

      .content {
        background: linear-gradient(-90deg, #f6ddf7, #eec5f8d7);
        box-shadow: 0 -0.1rem 0.5rem #f5deb4;

        .sub-title,
        .sub-text {
          filter: drop-shadow(0rem 0.2rem 0.2rem #f3edd5);
        }

        .sub-title {
          text-shadow: 0 0 0.1rem white;
          background: linear-gradient(0deg, #af6bc0fb, #fae8fa);
        }
      }

      .ending {
        border-bottom: solid 0.2rem;
        border-top: solid 0.2rem;
        border-color: white;
      }
    }

    &.technologies {
      .background {
        .title {
          background: #b3b1b19d;
          color: #020202;
        }
      }
      .content {
        background: linear-gradient(90deg, #eec5e4e0, #fac9ff, #eec5e4e0);
      }
      .container {
        .sub-title {
          text-shadow: 0 0 0.1rem white;
          border-color: #fffefc;
          background: linear-gradient(0deg, #e8f3b6, #fab15ee1);
        }
      }
    }
  }

  .list {
    background: linear-gradient(90deg, #faa14e, #fc8966);
    filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
  }
}

.outsourcing {
