        }
      }
      .container {
        .sub-title {
          margin-top: 2rem;
          border-radius: 0.5rem;
          padding: 0.1rem;
          width: 70vw;
          filter: drop-shadow(0 0.1rem 0.1rem #535353);
          background: linear-gradient(90deg, #fdc063, #f7e061);
        }
      }
      .ending {
        text-align: end;
        border-radius: 1rem;
        background: linear-gradient(-90deg, #71727134, #d8ede400);
        filter: drop-shadow(0 0.1rem 0.1rem #ffffff);
